import React, {Component} from 'react';
import {
    List,
    ListItem,
    ListItemText,
    Divider,
    AppBar,
    IconButton,
    SwipeableDrawer,
    Toolbar,
    Avatar,
    Typography
} from '@material-ui/core';
import {NavLink} from 'react-router-dom';
import logo from '../images/logo.png';
import {
    Menu as MenuIcon
} from '@material-ui/icons';
import {withStyles} from '@material-ui/core/styles';
import routeData from './routeData';

const style = theme => ({
    list: {
        width: 250
    },
    activeMenu: {
        backgroundColor: theme.palette.action.selected
    },
    sectionMobile: {
        display: 'flex',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    toolbar: theme.mixins.toolbar
});

class LocalDrawer extends Component {
    state = {
        left: false,
        menus: routeData
    };

    toggleDrawer = (side, open) => () => {
        this.setState({
            [side]: open,
        });
    };

    render() {
        const {classes} = this.props;
        const sideList = (
            <div className={classes.list}>
                <AppBar>
                    <Toolbar>
                        <Avatar src={logo} alt="启蒙幼儿园"></Avatar>
                        <Typography color="inherit" variant="h6" className={classes.grow}>
                            启蒙幼儿园
                    </Typography>
                    </Toolbar>
                </AppBar>
                <div className={classes.toolbar}></div>
                <Divider />
                <List>
                    {
                        this.state.menus.map(menu => {
                            return <ListItem component={NavLink} button to={menu.link} activeClassName={classes.activeMenu} key={menu.label}>
                                <ListItemText primary={menu.label} />
                            </ListItem>
                        })
                    }
                </List>
            </div>
        );

        return (
            <div className={classes.sectionMobile}>
                <IconButton onClick={this.toggleDrawer('left', true)} color={'inherit'}>
                    <MenuIcon />
                </IconButton>
                <SwipeableDrawer
                    open={this.state.left}
                    onClose={this.toggleDrawer('left', false)}
                    onOpen={this.toggleDrawer('left', true)}>
                    <div
                        tabIndex={0}
                        role="button"
                        onClick={this.toggleDrawer('left', false)}
                        onKeyDown={this.toggleDrawer('left', false)}>
                        {sideList}
                    </div>
                </SwipeableDrawer>
            </div>
        );
    }
}
export default withStyles(style)(LocalDrawer);
