import React, {Component} from 'react';
import {Typography} from '@material-ui/core';
import {withStyles} from '@material-ui/core/styles';
import aboutImages from '../images/about';

const headerText = '优质办园、追求卓越、精心养育、幼儿至上、造福学前、服务社会。';
const sections = [{
    title: '启蒙幼儿园',
    description: '启蒙幼儿园创办于1999年9月，是一所全日制民办幼儿园， 占地面积2000平方米，园内设备设施齐全，环境优雅，有独立的操场并全部软化。一切以孩子为中心，以拼玩操作、形象体验为特点的全面素质教育，赢得众多家长的信赖。',
    image: aboutImages.graduate
}, {
    title: '亲子早教',
    description: '2016年增加亲子早教，服务0-3岁全日制托育，小时制的亲子课程和半日托班等多元化的家庭服务项目。解决家庭育儿问题，成为中国家庭育儿好帮手，拥有丰富的亲子课程，为家庭提供寓教于乐的环境，成为孩子从家庭生活迈向集体生活的第一步。',
    image: aboutImages.earlyEdu
}, {
    title: '启蒙人生',
    description: '为了给孩子奠定良好的基础，启蒙幼教人刻苦努力，勇于创新，爱岗敬业，无私奉献，积极开展各项工作，把智慧和爱心奉献给所有孩子，为孩子一生做准备。注重家园合作，成立家委会，膳食委员会，保证孩子营养均衡，始终坚持依法办园以德立园，认真贯彻落实《幼儿园工作规程》和《幼儿园教育指导纲要》遵循《3-6岁儿童发展与学习指南》，努力完成“育人”和“服务”的双重任务。培养幼儿从小学会做人、做事，学会学习、创造，学会交流、合作。并开设体智能课程，让小朋友从小加强体育锻炼，增强体质，为一生成长做准备。',
    image: aboutImages.play
}];

const secSections = [{
    title: '理念',
    description: '在生活中学习，在游戏中学习，在快乐中学习',
    image: aboutImages.play1
}, {
    title: '方向',
    description: '科学、简单、快乐、高效',
    image: aboutImages.welcome
}, {
    title: '目标',
    description: '充满民主 、自由、幼儿喜爱的乐园',
    image: aboutImages.play3
}, {
    title: '特色',
    description: '幼儿礼仪、魔法拼摆、泡泡泥智慧指、体智能、启蒙哈奇早教。',
    image: aboutImages.bubbles
}];

const styles = theme => ({
    header: {
        background: 'no-repeat url(' + aboutImages.house + ')',
        backgroundSize: 'cover',
        height: '45vh',
        color: theme.palette.primary.contrastText,
        display: 'flex',
        alignItems: 'center'
    },
    headerText: {
        width: '100%',
        textAlign: 'center'
    },
    container: {
        width: '95%',
        maxWidth: '900px',
        margin: '0 auto',
        color: theme.palette.primary.contrastText
    },
    content: {
        padding: 3 * theme.spacing.unit + 'px 0',
        display: 'flex',
        alignItems: 'center'
    },
    contentTitle: {
        color: theme.palette.primary.contrastText,
        width: '100%',
        textAlign: 'center'
    },
    secSection: {
        display: 'inline-block',
        width: '25%',
        float: 'left',
        padding: theme.spacing.unit,
        boxSizing: 'border-box'
    },
    imgItem: {
        width: '100%'
    },
    contentImg: {
        width: '30%',
        display: 'none',
        [theme.breakpoints.up('md')]: {
            display: 'inline-block'
        }
    },
    contentDescription: {
        padding: theme.spacing.unit * 2,
        textIndent: theme.spacing.unit * 4,
        color: theme.palette.primary.contrastText,
        lineHeight: 2
    }
})

class About extends Component {

    render() {
        const {classes} = this.props;
        return (
            <div>
                <div className={classes.header}>
                    <Typography className={classes.headerText} color="inherit" variant="title">
                        {headerText}
                    </Typography>
                </div>
                <div className={classes.container}>
                    {
                        sections.map((section, index) => {
                            var firstEle = <img className={classes.contentImg} src={section.image} alt="启蒙幼儿园" />,
                                secEle = <div>
                                    <Typography className={classes.contentTitle} variant="h4">
                                        {section.title}
                                    </Typography>
                                    <Typography variant="body1" className={classes.contentDescription}>
                                        {section.description}
                                    </Typography>
                                </div>

                            if (index % 2 === 1) {
                                var temp = firstEle;
                                firstEle = secEle;
                                secEle = temp;
                            }
                            return <section className={classes.content} key={Math.random()}>
                                {firstEle}
                                {secEle}
                            </section>
                        })
                    }
                    <div>
                        {
                            secSections.map(sec => {
                                return <section className={classes.secSection} key={Math.random()} >
                                    <img src={sec.image} alt="活动" className={classes.imgItem} />
                                    <Typography variant="body2" color="inherit">
                                        {sec.description}
                                    </Typography>
                                </section>
                            })
                        }
                    </div>
                </div>
            </div>
        );
    }
}

export default withStyles(styles)(About);
