import React, {Component} from 'react';
import logo from './images/logo.png';
import {HashRouter as Router, NavLink} from 'react-router-dom';
import {
    AppBar,
    Toolbar,
    Avatar,
    Typography,
    ButtonBase
} from '@material-ui/core';
import Menus from './components/menus';
import Routes from './components/routes';
import {withStyles} from '@material-ui/core/styles';
import Footer from './components/footer'
import './app.css';
import LocalDrawer from './components/drawer';
import Title from './components/title';
var styles = theme => ({
    root: {
        flexGrow: 1,
    },
    toolbar: theme.mixins.toolbar,
    sectionMobile: {
        display: 'flex',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    sectionDesktop: {
        display: 'none',
        [theme.breakpoints.up('md')]: {
            display: 'inline',
        }
    },
    title: {
        flexGrow: 1
    }
});

class App extends Component {
    render() {
        const {classes} = this.props;
        return (
            <Router>
                <div className={classes.root}>
                    <AppBar position="fixed">
                        <Toolbar>
                            <LocalDrawer></LocalDrawer>
                            <Avatar className={classes.sectionDesktop} component={NavLink} to={'/home'} src={logo} alt="启蒙幼儿园"></Avatar>
                            <Typography color="inherit" variant="h6" className={classes.title}>
                                <ButtonBase component={NavLink} to={'/home'} className={classes.sectionDesktop}>
                                    启蒙幼儿园
                                </ButtonBase>
                                <Title className={classes.sectionMobile}/>
                            </Typography>
                            <Menus />
                        </Toolbar>
                    </AppBar>
                    <main>
                        <div className={classes.toolbar}></div>
                        <Routes />
                    </main>
                    <Footer />
                </div>
            </Router>
        );
    }
}

export default withStyles(styles)(App);
